<template>
  <header class="main-header full">
    <router-link to="/"><section class="logo">Toy Shop</section></router-link>
    <div class="nav">
      <router-link to="/">Home</router-link>
      <router-link to="/about">About</router-link>
      <router-link to="/toy">Toy shop</router-link>
      <router-link to="/reviews">Reviews Explore</router-link>
      <router-link v-if="user.isAdmin" to="/toy/dashboard"
        >Dashboard</router-link
      >
    </div>
    <p>
      Welcome {{ user.fullName }}
      <button v-if="user.fullName !== 'Guest'" @click="logout">Logout</button>
    </p>
  </header>
</template>

<script>
import { userService } from "../services/user.service.js";

export default {
  computed: {
    user() {
      let user = this.$store.getters.user;
      if (user) return user;
      else {
        return (user = {
          fullName: "Guest",
        });
      }
    },
  },
  methods: {
    logout() {
      console.log("LOGOUT");
      userService.logout().then(() => {
        // this.user = null;
        this.$router.push("/");
        this.$store.dispatch({ type: "setUser" });
      });
    },
  },
};
</script>

