<template>
  <section class="toy-preview">
    <h4>
      {{ toy.name }} <span>${{ toy.price }} </span>
    </h4>
    <img :src="toy.url" alt="" />

    <div class="preview-actions">
      <button>
        <router-link :to="'/toy/' + toy._id">Details</router-link>
      </button>
      <button v-if="user && user.isAdmin">
        <router-link :to="'/toy/edit/' + toy._id">Edit</router-link>
      </button>
      <button @click="removeToy" v-if="user && user.isAdmin">Delete</button>
    </div>
  </section>
</template>

<script>
import { eventBusService } from "../services/event-bus.service.js";
import { userService } from "../services/user.service";

export default {
  props: {
    toy: {
      type: Object,
    },
  },
  methods: {
    editToy() {
      eventBusService.$emit("editToy", this.toyToEdit);
    },
    removeToy() {
      eventBusService.$emit("removeToy", this.toyToEdit._id);
    },
  },
  computed: {
    toyToEdit() {
      return JSON.parse(JSON.stringify(this.toy));
    },
    user() {
      return this.$store.getters.user;
    },
  },
};
</script>
