<template>
  <section v-if="toyToEdit">
    <router-link to="/toy">Back</router-link>
    <div class="toy-details" v-if="toyToEdit">
      <form @submit.prevent="submitToy">
        <input type="text" placeholder="Toy name" v-model="toyToEdit.name" />
        <input
          type="number"
          placeholder="Price"
          v-model.number="toyToEdit.price"
        />
        <el-select v-model="toyToEdit.labels" multiple placeholder="Select">
          <el-option
            v-for="label in labels"
            :key="label.idx"
            :label="label.label"
            :value="label.value"
          >
          </el-option>
        </el-select>
        <p @click.prevent="toyToEdit.inStock = !toyToEdit.inStock">
          {{ toyToEdit.inStock ? "In Stock" : "Currently out of Stock" }}
        </p>
        <input type="text" placeholder="Img url" v-model="toyToEdit.url" />
        <img-upload @onSaveImg="saveImg"></img-upload>

        <button>Submit</button>
      </form>
    </div>
  </section>
</template>

<script>
import { toyService } from "../services/toy.service.js";
import imgUpload from "@/cmps/img-upload";

export default {
  data() {
    return {
      toyToEdit: null,
      labels: [
        { value: "On wheels", label: "On wheels" },
        { value: "Box game", label: "Box game" },
        { value: "Art", label: "Art" },
        { value: "Baby", label: "Baby" },
        { value: "Doll", label: "Doll" },
        { value: "Outdoor", label: "Outdoor" },
        { value: "Puzzle", label: "Puzzle" },
      ],
      showLabels: false,
    };
  },
  async created() {
    const { toyId } = this.$route.params;
    console.log("toyId", toyId);
    if (toyId !== "newToy") {
      try {
        const toy = await toyService.getById(toyId);
        this.toyToEdit = JSON.parse(JSON.stringify(toy));
      } catch (err) {
        console.log("cant get toy");
      }
    } else this.toyToEdit = toyService.getEmptyToy();
  },
  methods: {
    async submitToy() {
      let toy = JSON.parse(JSON.stringify(this.toyToEdit));
      if (!toy._id) {
        toy.createdAt = Date.now();
        try {
          await this.$store.dispatch({ type: "addToy", toy });
          this.$router.push("/toy");
        } catch (err) {
          console.log("eror", err);
        }
      } else {
        try {
          await this.$store.dispatch({ type: "updateToy", toy });
          this.$router.push("/toy");
        } catch (err) {
          console.log("eror", err);
        }
      }
    },
    saveImg(imgUrl) {
      this.toyToEdit.url = imgUrl;
    },
  },
  computed: {
    timeToShow() {
      return new Date(this.toyToEdit.createdAt)
        .toLocaleString()
        .substring(0, 17);
    },
  },
  components: {
    imgUpload,
  },
};
</script>
