import { httpService } from './http.service'

// const TOY_URL = 'http://localhost:3030/api/toy/'

export const toyService = {
    query,
    getById,
    remove,
    save,
    getEmptyToy
}

async function query(filterBy) {
    try {
        // const res = await axios.get(TOY_URL, { params: filterBy })
        // return res.data
        // return httpService.get(`review${queryStr}`)
        return httpService.get('toy/', filterBy)

    } catch (err) {
        console.log('error:', err)
    }
}

async function getById(id) {
    try {
        return httpService.get('toy/' + id)
        // const res = await axios.get(TOY_URL + id)
        // return res.data
    } catch (err) {
        console.log('error:', err)
    }
}

async function remove(id) {
    try {
        return httpService.delete('toy/' + id)
        // const res = await axios.delete(TOY_URL + id)
        // return res.data
    } catch (err) {
        console.log('error:', err)
    }
}

async function save(toy) {
    try {
        if (toy._id) {
            return httpService.put('toy/' + toy._id, toy)
            // const res = await axios.put(TOY_URL + `${toy._id}`, toy)
            // return res.data
        } else {
            return httpService.post('toy/', toy)
            // const res = await axios.post(TOY_URL, toy)
            // return res.data
        }
    } catch (err) {
        console.log('err', err)
    }
}

function getEmptyToy() {
    return {
        name: '',
        price: null,
        labels: [],
        createdAt: null,
        inStock: true,
        url: ''
    }
}