<script>
import { Doughnut } from "vue-chartjs";

export default {
  extends: Doughnut,
  mounted() {
    // Overwriting base render method with actual data.
    this.renderChart({
      labels: ["Art", "Baby", "Doll", "Puzzle", "Outdoor"],
      datasets: [
        {
          label: "GitHub Commits",
          backgroundColor: [
            "#ff9d9d",
            "#ffe293",
            "#d9ffad",
            "#8ff9ff",
            "#f67aff",
          ],
          data: this.test,
        },
      ],
    });
  },
  methods: {
    labelNum(label) {
      const toys = this.$store.getters.toys;
      return toys.filter((toy) => {
        return toy.labels.some(
          (lbl) => label.toLowerCase() === lbl.toLowerCase()
        );
      }).length;
    },
  },
  computed: {
    test() {
      return [
        this.labelNum("Art"),
        this.labelNum("Baby"),
        this.labelNum("Doll"),
        this.labelNum("Puzzle"),
        this.labelNum("outdoor"),
      ];
    },
    randomColor() {
      var randomColor = Math.floor(Math.random() * 16777215).toString(16);
      return "#" + randomColor;
    },
  },
};
</script>

