<template>
  <section>
    <router-link to="/toy">Back</router-link>
    <div class="toy-details" v-if="toy">
      <h3>{{ toy.name }}</h3>
      <span>Price: ${{ toy.price }}</span>
      <section>
        <span v-if="toy.labels">Labels:</span>
        <span v-for="(lable, idx) in toy.labels" :key="lable">
          {{ lable }}
          <span v-show="idx !== toy.labels.length - 1">|</span>
        </span>
      </section>
      <span>{{ toy.inStock ? "In Stock" : "Currently out of Stock" }}</span>
      <span>Created At:{{ timeToShow }}</span>
      <small>Id: {{ toy._id }}</small>
      <button @click="reviewAdd = !reviewAdd">Add review</button>
      <button @click="showChat = !showChat">Chat</button>
      <section v-if="reviewAdd" class="add-review">
        Thank for adding your review!
        <form @submit.prevent="submitReview">
          <textarea
            v-model="reviewToEdit.txt"
            rows="5"
            type="text"
            placeholder="Your review comes here"
          /><br />
          <button>Submit</button>
          <button @click="reviewAdd = !reviewAdd">Cancel</button>
        </form>
      </section>
      <h3 v-if="reviewsToShow">Reviews:</h3>
      <section class="toy-reviews">
        <span v-for="(review, idx) in reviewsToShow" :key="idx">
          <p>{{ review.txt }}</p>
          <p>
            Review by
            <router-link :to="'/user/' + review.byUser.fullName">
              {{ review.byUser.fullName }}
            </router-link>
          </p>
        </span>
      </section>
      <chat v-show="showChat" :toy="toy" class="chat" />
    </div>
  </section>
</template>

<script>
import { eventBusService, showMsg } from "../services/event-bus.service.js";
import { toyService } from "../services/toy.service.js";
import chat from "../pages/chat.vue";
import { reviewService } from "../services/review.service.js";

export default {
  data() {
    return {
      // id: this.$route.params.toyId,
      reviewAdd: false,
      // reviewToEdit: null,
      reviewToEdit: {
        txt: "",
        aboutToyId: null,
      },
      toy: null,
      // reviews: [],
      showChat: false,
    };
  },
  async created() {
    const { toyId } = this.$route.params;
    try {
      const toy = await toyService.getById(toyId);
      this.toy = toy;
      this.reviewToEdit.aboutToyId = toy._id;
      // this.reviews = this.$store.getters.reviews;

      // this.toy["reviews"] = [
      //   { txt: "Great toy!" },
      //   { txt: "Broke after two days, not worth the price!" },
      // ];
      // return toy;
    } catch (err) {
      console.log("can't load toy");
    }
  },
  methods: {
    async submitReview() {
      try {
        await this.$store.dispatch({
          type: "addReview",
          review: this.reviewToEdit,
        });
        this.$store.dispatch({ type: "loadReviews" });
        this.reviewToEdit = { txt: "", aboutToyId: null };
        this.reviewAdd = false;
        // this.$router.push("/toy/");
      } catch (err) {
        console.log("err:", err);
      }
    },
  },
  computed: {
    timeToShow() {
      return new Date(this.toy.createdAt).toLocaleString().substring(0, 17);
    },
    reviewsToShow() {
      let reviews = this.$store.getters.reviews;
      return reviews.filter((review) => review.aboutToy._id === this.toy._id);
    },
  },
  components: {
    chat,
  },
};
</script>
