import { toyService } from '../services/toy.service.js';

export const toyStore = {
  state: {
    toys: [],
    filterBy: {
      txt: '',
      status: 'all',
      labels: '',
    },
    isLoading: false,
    sortBy: 'date',
  },
  getters: {
    toys(state) {
      let toys = JSON.parse(JSON.stringify(state.toys))
      return toys;
    },
    isLoading({ isLoading }) {
      return isLoading;
    },
  },
  mutations: {
    setToys(state, { toys }) {
      state.toys = toys;
    },
    addToy(state, { toy }) {
      state.toys.unshift(toy);
    },
    updateToy(state, { toy }) {
      const idx = state.toys.findIndex((currToy) => currToy._id === toy._id);
      state.toys.splice(idx, 1, toy);
    },
    removeToy(state, { toyId }) {
      const idx = state.toys.findIndex((currToy) => currToy._id === toyId);
      state.toys.splice(idx, 1);
    },
    setFilter(state, { filterBy }) {
      state.filterBy = filterBy;
    },
    setSort(state, { sortBy }) {
      state.sortBy = sortBy;
    },
    setLoading(state, { isLoading }) {
      state.isLoading = isLoading;
    },
  },
  actions: {
    async loadToys({ commit, state }) {
      try {
        commit({ type: 'setLoading', isLoading: true });
        const toys = await toyService.query(state.filterBy, state.sortBy)
        commit({ type: 'setToys', toys });
      } catch (err) {
        console.log('Can\'t load toys', err)
      } finally {
        commit({ type: 'setLoading', isLoading: false });
      }
    },
    async addToy({ commit, dispatch }, { toy }) {
      try {
        commit({ type: 'setLoading', isLoading: true });
        const savedToy = await toyService.save(toy)
        commit({ type: 'addToy', toy: savedToy })
        return savedToy;
      } catch (err) {
        console.log('Can\'t add toy', err)
      } finally {
        commit({ type: 'setLoading', isLoading: false });
      }
    },
    async updateToy({ commit, dispatch }, { toy }) {
      try {
        commit({ type: 'setLoading', isLoading: true });
        const savedToy = await toyService.save(toy)
        commit({ type: 'updateToy', toy: savedToy });
        return savedToy;
      } catch (err) {
        console.log('Can\'t update toy', err)
      } finally {
        commit({ type: 'setLoading', isLoading: false });
      }
    },
    async removeToy({ commit, dispatch }, { toyId }) {
      try {
        commit({ type: 'setLoading', isLoading: true });
        await toyService.remove(toyId)
        commit({ type: 'removeToy', toyId });
      } catch (err) {
        console.log('Can\'t remove toy', err)
      } finally {
        commit({ type: 'setLoading', isLoading: false });
      }
    },
    setFilter({ commit, dispatch }, { filterBy }) {
      commit({ type: 'setFilter', filterBy });
      dispatch({ type: 'loadToys' });
    },
    setSort({ commit }, { sortBy }) {
      commit({ type: 'setSort', sortBy });
    },
  },
};
