<template>
  <div class="home">
    <h1>Welcome to the best Toy shop!</h1>
    <section class="login-page">
      <h2 @click="toggleForm('login')">Login</h2>
      <form v-if="loginForm" class="login" @submit.prevent="login">
        <!-- <ValidationProvider v-model="loginNickname">
          <div slot-scope="{ errors }">
            <input type="text" placeholder="Username" />
            <p>{{ errors[0] }}</p>
          </div>
        </ValidationProvider> -->
        <input
          type="text"
          placeholder="Username"
          v-model="loginNickname"
        /><br />
        <input
          type="text"
          placeholder="Password"
          v-model="loginPassword"
        /><br />
        <button>Login</button>
      </form>
      <h2 @click="toggleForm('signup')">Signup</h2>
      <form v-if="signupForm" class="signup" @submit.prevent="signup">
        <input
          type="text"
          placeholder="Full name"
          v-model="signupFullname"
        /><br />
        <input
          type="text"
          placeholder="Username"
          v-model="signupNickname"
        /><br />
        <input
          type="text"
          placeholder="Password"
          v-model="signupPassword"
        /><br />
        <button>Signup</button>
      </form>
    </section>
  </div>
</template>

<script>
import { userService } from "../services/user.service.js";
import { ValidationProvider } from "vee-validate";

export default {
  name: "Home",

  data() {
    return {
      user: null,
      loginForm: false,
      loginNickname: "",
      signupNickname: "",
      signupFullname: "",
      signupForm: false,
      loginPassword: "",
      signupPassword: "",
    };
  },
  created() {
    this.user = userService.getLoggedinUser();
    // if (user) userService.logout();

    // const user = userService.getLoggedinUser();
  },
  methods: {
    async login() {
      if (this.user) {
        console.log("Logout first");
        return;
      }
      try {
        const user = await userService.login(
          this.loginNickname,
          this.loginPassword
        );
        this.user = user;
        if (user) this.$router.push("/toy");
      } catch (err) {
        console.log("wrong pass/username", err);
      }
    },
    async signup() {
      if (this.user) {
        console.log("Logout first");
        return;
      }
      try {
        const user = await userService.signup(
          this.signupNickname,
          this.signupPassword,
          this.signupFullname
        );
        this.user = user;
        if (user) this.$router.push("/toy");
      } catch (err) {
        console.log("taken username", err);
      }
    },
    toggleForm(form) {
      if (form === "login") {
        this.loginForm = !this.loginForm;
        this.signupForm = false;
      } else {
        this.signupForm = !this.signupForm;
        this.loginForm = false;
      }
    },
  },
  computed: {},
  components: {
    userService,
    ValidationProvider,
  },
};
</script>
