<template>
  <!-- <template> -->
  <section class="toy-filter">
    <el-input
      type="text"
      @input="setFilter"
      placeholder="Search a toy..."
      v-model="filterBy.txt"
    />

    <el-select
      @change="setFilter"
      v-model="filterBy.status"
      collapse-tags
      placeholder="In/Out of stock"
    >
      <el-option value="">All</el-option>
      <el-option value="In stock">In stock</el-option>
      <el-option value="Out of stock">Out of stock</el-option>
    </el-select>

    <label>
      <el-select
        @change="setFilter"
        v-model="filterBy.labels"
        multiple
        placeholder="Search by labels"
      >
        <!-- <el-option value="">All</el-option> -->
        <el-option value="On wheels">On wheels</el-option>
        <el-option value="Box game">Box game</el-option>
        <el-option value="Art">Art</el-option>
        <el-option value="Baby">Baby</el-option>
        <el-option value="Doll">Doll</el-option>
        <el-option value="Outdoor">Outdoor</el-option>
        <el-option value="Puzzle">Puzzle</el-option>
      </el-select>
    </label>

    <label>
      <el-select
        @change="setFilter"
        v-model="filterBy.sort"
        placeholder="Sort by..."
      >
        <el-option value="Name">Name</el-option>
        <el-option value="Price Low">Price (low to high)</el-option>
        <el-option value="Price High">Price (high to low)</el-option>
        <el-option value="Date">Date</el-option>
      </el-select>
    </label>
  </section>
</template>

<script>
export default {
  data() {
    return {
      filterBy: {
        status: "",
        txt: "",
        labels: [],
        sort: "",
      },
      sortBy: "",
      options: ["All", "In stock", "Out of stock"],
    };
  },
  methods: {
    setFilter() {
      const filterBy = JSON.parse(JSON.stringify(this.filterBy));
      this.$store.dispatch({
        type: "setFilter",
        filterBy,
      });
    },
    setSort() {
      this.$store.dispatch({ type: "setSort", sortBy: this.sortBy });
    },
  },
};
</script>
