<template>
  <section class="toy-app">
    <h1>Toy Shop</h1>
    <button v-if="user && user.isAdmin">
      <router-link to="/toy/edit/newToy">Add Toy</router-link>
    </button>
    <toy-filter></toy-filter>
    <div class="add-container"></div>
    <p v-if="isLoading">Loading...</p>
    <toy-list v-else :toys="toys"></toy-list>
  </section>
</template>

<script>
import toyFilter from "../cmps/toy-filter.cmp.vue";
import { eventBusService, showMsg } from "../services/event-bus.service.js";
import { toyService } from "../services/toy.service.js";
import toyList from "../cmps/toy-list.cmp.vue";
import { userService } from "../services/user.service";

export default {
  data() {
    return {
      user: null,
    };
  },
  created() {
    eventBusService.$on("removeToy", this.deleteToy);
    this.$store.dispatch({ type: "setUser" });
    this.user = userService.getLoggedinUser();
  },
  destroyed() {
    eventBusService.$off("removeToy", this.deleteToy);
  },
  computed: {
    toys() {
      return this.$store.getters.toys;
    },
    isLoading() {
      return this.$store.getters.isLoading;
    },
  },
  methods: {
    async deleteToy(toyId) {
      try {
        await this.$store.dispatch({ type: "removeToy", toyId });
        showMsg(`Toy deleted`);
      } catch (err) {
        console.log("eror:", err);
      }
    },
  },
  components: {
    toyList,
    toyFilter,
  },
};
</script>
