import Vue from 'vue';
import VueRouter from 'vue-router';
import home from '../pages/home.vue';
import about from '../pages/about.vue';
import toyApp from '../pages/toy-app.vue';
import toyDetails from '../pages/toy-details.vue'
import userDetails from '../pages/user-details.vue'
import toyEdit from '../pages/toy-edit.vue'
import toyDashboard from '../pages/toy-dashboard.vue'
import reviews from '../pages/reviews.vue'


Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: home,
  },
  {
    path: '/about',
    name: 'About',
    component: about,
  },
  {
    path: '/reviews',
    name: 'Reviews',
    component: reviews,
  },
  {
    path: '/toy',
    name: 'toy-app',
    component: toyApp,
  },
  {
    path: '/toy/dashboard',
    name: 'toy-dashboard',
    component: toyDashboard,
  },
  {
    path: '/toy/edit/:toyId',
    name: 'toy-edit',
    component: toyEdit,
  },
  {
    path: '/toy/:toyId',
    name: 'toy-details',
    component: toyDetails,
  },
  {
    path: '/user/:userId',
    name: 'user-details',
    component: userDetails,
  },
];

const router = new VueRouter({
  routes,
});

export default router;
