<template>
  <div class="about">
    <h1>Come visit our shops!</h1>
    <GmapMap
      ref="mapRef"
      :center="center"
      :zoom="10"
      map-type-id="terrain"
      style="width: 60%; height: 400px"
    >
      <GmapMarker
        :key="index"
        v-for="(m, index) in markers"
        :position="m.position"
        :clickable="true"
        :draggable="true"
        @click="center = m.position"
      />
    </GmapMap>
  </div>
</template>

<script>
// import { gmapApi } from "vue2-google-maps";
export default {
  data() {
    return {
      markers: [
        {
          markers: [
            {
              city: "Petach Tikva",
              position: { lat: 32.09573, lng: 34.881865 },
            },
            { city: "Tel Aviv", position: { lat: 32.08704, lng: 34.775293 } },
            { city: "Netanya", position: { lat: 32.290295, lng: 34.861591 } },
          ],
          center: { lat: 32.08171353971608, lng: 34.78886754010946 },
        },
      ],
    };
  },
  mounted() {
    // At this point, the child GmapMap has been mounted, but
    // its map has not been initialized.
    // Therefore we need to write mapRef.$mapPromise.then(() => ...)

    this.$refs.mapRef.$mapPromise.then((map) => {
      map.panTo({ lat: 1.38, lng: 103.8 });
    });
  },
  computed: {
    // google: gmapApi,
  },
};
</script>
:position="m.position"
